import React from 'react';
import Align from 'rc-align';
import ReactDOM from 'react-dom';


class Demo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      align: {
        points: ['cc', 'cc']
      }
    };
  }


  render() {
    return (
      <Align
        align={this.state.align}
      >
        <div style={{
          position: 'absolute',
          width: 100,
          height: 100,
          background: 'yellow',
        }}>
          target is window
        </div>
      </Align>
    );
  }
}


ReactDOM.render(<Demo />, document.getElementById('demo'));
